@import '../../common/colours';

.dialogWrapper {
  min-width: 22rem;
  padding: 1.5rem;
  border-radius: 1rem !important;
  position: relative;

  .closeIcon {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
  }

  h2 {
    font-size: 1.2rem;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
  }

  .note {
    font-size: 0.75rem;
    font-style: italic;
    color: rgba($color: $app--black, $alpha: 0.5);
  }
}
