@import '../../common/screen_size';
@import '../../common/colours';

.sitemapPageWrap {
  h4 {
    font-weight: 600;

    @media (max-width: $sm) {
      font-size: 1.25rem;
    }
  }

  .listWrap {
    display: flex;
    flex-wrap: wrap;

    margin-bottom: 2rem;

    .group {
      margin: 1rem 0;
      width: calc(100% / 3);

      @media (max-width: $md) {
        width: calc(100% / 2);
      }

      @media (max-width: $xs) {
        width: 100%;
      }

      span {
        display: block;
        width: fit-content;
      }

      .title {
        padding-bottom: 0.7rem;
      }

      .link {
        cursor: pointer;
        color: $app--primary--01;
        padding-bottom: 0.5rem;
      }
    }
  }
}
