@import '../../common/colours';

.dialogWrapper {
  min-width: 22rem;
  padding: 1.5rem;
  border-radius: 1rem !important;
  position: relative;

  .closeIcon {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
  }
  .forgotPassword {
    font-size: 1rem;
    font-weight: 500;
    color: $app--primary--01;
  }

  a {
    text-decoration: none;
  }

  .avatarWrapper {
    font-size: 1rem;
    margin-bottom: 1rem;
    text-align: center;
    .avatarImg {
      border: 2px solid $app--primary--01;
      width: 3rem;
      height: 3rem;
      margin: auto;
    }
  }
}
